@font-face {
  font-family: "OpenSans";
  src: url("../../fonts/opensans.woff2") format("woff2"), url("../../fonts/opensans.woff") format("woff");
  font-style: normal;
  font-weight: normal;
}
@font-face {
  font-family: "OpenSans";
  src: url("../../fonts/opensansbold.woff2") format("woff2"), url("../../fonts/opensansbold.woff") format("woff");
  font-style: normal;
  font-weight: bold;
}
@font-face {
  font-family: "OpenSans";
  src: url("../../fonts/opensanslight.woff2") format("woff2"), url("../../fonts/opensanslight.woff") format("woff");
  font-style: normal;
  font-weight: 300;
}
body {
  margin: 0;
  min-width: 320px;
  font-family: "OpenSans", "Arial", sans-serif;
  color: #283645;
  font-size: 14px;
  line-height: 1.7em;
}
.link-normalize {
  text-decoration: none;
}
.list-item-reset {
  padding: 0;
  margin: 0;
}
.list-normalize {
  padding: 0;
  margin: 0;
  list-style: none;
}
.wrapper {
  position: relative;
  width: 280px;
  margin: 0 auto;
}
@media (min-width: 660px) {
  .wrapper {
    width: 620px;
  }
}
@media (min-width: 960px) {
  .wrapper {
    width: 940px;
  }
}
.page-header .wrapper {
  width: 100%;
}
.page-header__menu-container {
  background-color: #283645;
}
.page-header__pic {
  display: block;
  padding: 23px 20px;
}
.page-header__menu-toggle {
  position: absolute;
  top: 13px;
  right: 10px;
  display: inline-block;
  padding: 20px 20px;
  width: 23px;
  height: 24px;
  border: 0;
  font-size: 0;
  vertical-align: middle;
  background-color: transparent;
}
.page-header__menu-toggle::before,
.page-header__menu-toggle::after {
  position: absolute;
  top: 5px;
  left: 10px;
  content: "";
  height: 30px;
  width: 3px;
  background-color: #ffffff;
}
.page-header__menu-toggle::before {
  transform: rotate(45deg);
}
.page-header__menu-toggle::after {
  transform: rotate(-45deg);
}
@media (min-width: 660px) {
  .page-header__menu-toggle {
    top: 30px;
  }
  .page-header__pic {
    width: 147px;
    height: 40px;
    padding-top: 27px;
    padding-bottom: 42px;
  }
}
@media (min-width: 960px) {
  .page-header__menu-container .wrapper {
    display: flex;
  }
  .page-header .wrapper {
    width: 940px;
    margin: 0 auto;
  }
  .page-header__pic {
    width: 147px;
    height: 40px;
    padding-left: 0;
    padding-right: 0;
  }
  .page-header__menu-container {
    position: relative;
    z-index: 1;
    margin-bottom: -110px;
    background-color: rgba(0, 0, 0, 0.3);
  }
}
.site-menu__list {
  padding: 0;
  margin: 0;
  list-style: none;
}
.site-menu__item {
  display: flex;
  justify-content: center;
  align-items: center;
  border-top: 1px solid #485461;
  border-bottom: 1px solid #000000;
}
.site-menu__link {
  text-decoration: none;
  position: relative;
  padding: 22px 0;
  text-transform: uppercase;
  color: #ffffff;
  font-size: 18px;
  line-height: 18px;
  font-weight: bold;
}
@media (min-width: 960px) {
  .site-menu__item {
    border: none;
  }
  .site-menu__link {
    padding-top: 46px;
    padding-bottom: 46px;
    font-weight: normal;
  }
  .page-header__menu-container .wrapper {
    justify-content: space-between;
  }
  .page-header__menu-toggle {
    display: none;
  }
  .site-menu {
    width: 620px;
    display: flex;
  }
  .site-menu__list {
    display: flex;
    width: 100%;
    justify-content: space-between;
  }
}
.button {
  text-decoration: none;
  display: inline-block;
  vertical-align: middle;
  padding: 15px 5px;
  border: 3px solid #ffffff;
  border-radius: 30px;
  text-align: center;
  color: #ffffff;
}
.button__add {
  width: 100%;
  box-sizing: border-box;
  padding: 16px 0;
  margin-bottom: 10px;
  border-color: #d22856;
  font-weight: bold;
  text-transform: uppercase;
  color: #d22856;
}
.button__cancel {
  width: 100%;
  margin-bottom: 10px;
  box-sizing: border-box;
  border-color: #dddfe0;
  background-color: #f2f2f2;
  font-weight: bold;
  text-transform: uppercase;
  color: #dddfe0;
}
@media (min-width: 960px) {
  .button__add {
    width: 180px;
  }
  .button__cancel {
    width: 180px;
  }
}
.download {
  padding-top: 30px;
  padding-bottom: 30px;
}
.download__title {
  display: none;
  text-align: center;
  color: #ffffff;
  font-size: 40px;
  line-height: 1.5em;
  font-weight: 300;
}
.download__btn {
  display: block;
  margin-bottom: 30px;
  text-align: center;
  font-size: 18px;
  line-height: 1em;
  font-weight: bold;
}
.download__list {
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  justify-content: space-around;
  margin-bottom: 15px;
}
.download__item {
  padding: 0;
  margin: 0;
  font-size: 0;
}
.download__link {
  display: inline-block;
  vertical-align: middle;
  width: 47px;
  height: 56px;
}
.download__link--appstore {
  background: url("../../img/logo-apple.svg") no-repeat center;
}
.download__link--playmarket {
  background: url("../../img/logo-android.svg") no-repeat center;
}
.download__link--widows {
  background: url("../../img/logo-microsoft.svg") no-repeat center;
}
.download__text {
  margin: 0;
  text-align: center;
  color: #ffffff;
  font-size: 14px;
  line-height: 1.7em;
  font-weight: 300;
}
@media (min-width: 660px) {
  .download {
    width: 300px;
  }
  .download__text {
    font-size: 18px;
    line-height: 1.7em;
  }
  .download__title {
    display: inline-block;
    padding-left: 35px;
    padding-right: 35px;
    margin-bottom: 55px;
    vertical-align: middle;
  }
  .download__btn {
    margin-bottom: 75px;
  }
  .download__list {
    margin-bottom: 60px;
  }
}
.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.promo {
  margin: 0;
  padding-top: 40px;
  padding-bottom: 30px;
}
.promo__text {
  display: block;
  margin: 0;
  text-align: center;
  font-size: 24px;
  line-height: 1.7em;
  font-weight: 300;
  color: #283645;
}
@media (min-width: 660px) {
  .promo {
    padding-top: 55px;
  }
  .promo__item {
    font-size: 40px;
  }
}
@media (min-width: 960px) {
  .promo__text {
    font-size: 50px;
    line-height: 1.1em;
  }
}
.features__content-container {
  background-color: #f2f2f2;
}
.features__list {
  padding: 0;
  margin: 0;
  list-style: none;
  position: relative;
  padding-top: 60px;
}
.features__item {
  display: block;
  margin-bottom: 30px;
  text-align: center;
}
.features__img {
  display: none;
  width: 100%;
  height: auto;
}
.features__title {
  font-size: 18px;
  line-height: 1em;
  font-weight: bold;
}
.features__text {
  font-size: 14px;
  line-height: 1.7em;
}
.features__wrapper::after {
  content: "";
  display: block;
  width: 160px;
  height: 280px;
  margin: 0 auto;
  background: url("../../img/iphone-screen-mobile@1x.png") no-repeat;
}
@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .features__wrapper::after {
    background: url("../../img/iphone-screen-mobile@2x.png") no-repeat;
    background-size: 160px 320px;
  }
}
@media (min-width: 660px) {
  .features__wrapper {
    display: flex;
    flex-direction: row-reverse;
  }
  .features__wrapper::after {
    content: "";
    display: block;
    width: 268px;
    height: 461px;
    margin: 0;
    margin-top: auto;
    margin-right: auto;
    background: url("../../img/iphone-screen-3-tablet@1x.png") no-repeat;
  }
  .features__item {
    width: 300px;
    margin-left: auto;
    text-align: left;
  }
  .features__list {
    padding-bottom: 40px;
  }
}
@media (min-width: 660px) and (min-resolution: 144dpi), (min-width: 660px) and (min-resolution: 1.5dppx) {
  .features__wrapper::after {
    background: url("../../img/iphone-screen-3-tablet@2x.png") no-repeat;
    background-size: 268px 461px;
  }
}
@media (min-width: 960px) {
  .features__wrapper {
    justify-content: center;
  }
  .features__wrapper::after {
    display: none;
  }
  .features__list {
    display: flex;
    justify-content: space-around;
  }
  .features__item {
    width: 240px;
    margin: 0;
  }
  .features__img {
    display: block;
  }
  .features__title {
    margin-top: 50px;
    margin-bottom: 25px;
    font-size: 30px;
    text-align: center;
  }
  .features__text {
    font-size: 18px;
    text-align: center;
  }
}
.photo__title {
