  display: flex;
  justify-content: center;
  font-size: 0;
}
.social__item {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  margin-right: 10px;
  border: 2px solid #f2f2f2;
