}
.social__link--tweeter path {
  fill: #d22856;
}
.social__link--facebook {
  position: relative;
  top: -3px;
}
.social__link--facebook path {
  fill: #d22856;
}
.social__link--youtube path {
  fill: #d22856;
}
.social__link--youtube {
  position: relative;
  left: 2px;
}
.page-main__title {
  margin: 0;
  padding: 0;
  color: #ffffff;
  text-align: center;
  font-size: 24px;
  font-weight: 300;
}
.page-main__title--photo {
  box-sizing: border-box;
  min-height: 170px;
  padding-top: 34px;
  line-height: 1.1em;
}
@media (min-width: 660px) {
  .page-main__title {
    padding-right: 50px;
    padding-left: 50px;
    padding-top: 100px;
