  }
}
.effect {
  display: none;
  padding-top: 60px;
  padding-bottom: 100px;
}
.effect__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.effect__timer {
  position: relative;
  display: flex;
  width: 240px;
  justify-content: center;
  align-items: center;
}
.effect__timer::before {
  content: "";
  position: absolute;
  top: 70px;
  box-sizing: border-box;
  width: 200px;
  height: 200px;
  border: 15px solid #d22856;
  border-radius: 50%;
}
.effect__timer::after {
  content: "24";
  position: absolute;
  top: 150px;
  color: #d22856;
  font-size: 96px;
  font-weight: bold;
}
.effect__text-container {
  width: 300px;
}
.effect__title {
  margin: 0;
  margin-bottom: 20px;
  padding-right: 10px;
  font-size: 30px;
  line-height: 1.2em;
}
.effect__text {
  margin: 0;
  margin-bottom: 15px;
  font-size: 14px;
  line-height: 1.71em;
}
.effect__link {
  color: #d22856;
  font-size: 14px;
  line-height: 1.7em;
}
@media (min-width: 660px) {
  .effect {
    display: block;
  }
}
@media (min-width: 960px) {
  .effect__wrapper {
    justify-content: flex-start;
  }
  .effect__timer {
    width: 300px;
    margin-right: 120px;
  }
  .effect__text-container {
    width: auto;
  }
}
.page-footer--index {
